.Nav {
    line-height: 50px;
    border-top: 1px solid var(--gc);
    border-bottom: 1px solid var(--gc);
}

.Nav ul {
    display: flex;
    justify-content: center;
}

.Nav ul>li {
    padding: 0 20px;
}

.Nav ul>li a.active {
    color: var(--mc)
}

.Nav ul>li~li {
    border-left: 1px solid var(--gc);
}

.about img {
    width: 100%;
    height: 400px;
    object-fit: fill;
}

.about .container {
    margin: 0 auto 100px auto;
    justify-content: space-between;
}

.about figure {
    width: 30%;
    height: 400px;
    overflow: hidden;
}


.About02 .top {
    float: left;
    width: 100%;
    margin: 0 0 50px 0;
}

.About02 .left {
    float: left;
    width: 50%;
    text-align: center;
    font-size: 25px;
}


.About02 .left::after {
    content: "";
    display: inline-block;
    width: 2px;
    height: 150px;
    background: var(--mc);

}

.About02 .right {
    float: left;
    width: 50%;
}



.Business .container img {
    width: 100%;
    height: 350px;
    object-fit: cover;
}

.Business figure {
    flex: 1;
    position: relative;
    overflow: hidden;
}

.Business figure~figure {
    border-left: 1px solid transparent;
}


.Business figure .cover {
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    background: rgba(0, 0, 0, 0.75);
    color: var(--lc);
    transition: left 0.5s;
    padding: 40px 10px;
}

.Business figure:hover .cover {
    left: 0;
}

.Business figure h3 {
    font-size: 21px;
    font-weight: 500;
    padding: 0 0 20px 0;
    margin: 0 0 20px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.Business figure p {
    font-size: 15px;
    font-weight: 300;
    line-height: 1.25;
}

.Business figure .cover a {
    /* display: inline-block; */
    align-self: flex-start;
    border: 1px solid rgba(255, 255, 255, 0.5);
    padding: 10px 15px;
    margin-top: auto;
}

.Business .inner>div {
    float: left;
    width: 50%;
    padding: 0 15px;
}

.Business .container {
    margin: 0 auto 100px auto;
}

.Business .tit strong {
    position: relative;
    display: block;
    font-size: 30px;
    font-weight: 700;
    margin: 0 0 20px 0;
    padding: 0 0 0 20px;
}

.Business .tit p {
    font-size: 15px;
    font-weight: 300;
    padding: 0 0 0 20px;
}

.Business .tit strong::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 80px;
    background: var(--mc);
}

.Business02 .container {
    gap: 50px;
    margin-bottom: 50px;
}

.Business02 .container>div {
    flex: 1;
}

.Business02 .left img {
    width: 100%;
    height: 400px;
    object-fit: cover;
}

.Business02 .right {
    display: flex;
    flex-direction: column;
    border-top: 2px solid var(--dc);
    padding: 40px 0 0 0;
}

.Business02 .right h3 {
    font-size: 31px;
    font-weight: 700;
    margin: 0 0 20px 0;
}

.Business02 .right p {
    margin: 0 0 30px 0;
}

.Business02 .right .bottom {
    margin: auto 0 0 0;
    padding: 20px 20px 40px 20px;
    background: var(--lc);
    border-top: 1px solid var(--gc);
    line-height: 1.2;
}

.Business02 .inner {
    padding: 0 15px;
}

.Business02 .inner h3 {
    font-size: 21px;
    font-weight: 500;
    margin: 0 0 30px 0;
}

.Business02 .inner h3::before {
    content: "/";
    color: var(--mc);
    font-size: 18px;
    font-weight: 900;
    margin: 0 10px 0 0;
    display: inline-block;
    transform: translateY(-10px) rotate(20deg);
}

.Business02 .inner ul {
    padding: 20px 0;
    border-top: 1px solid var(--dc);
    border-bottom: 1px solid var(--dc);
}

.Business02 .inner ul>li {
    float: left;
    width: 50%;
    padding: 5px 0;
}

.Business02 .inner ul>li::before {
    content: attr(data-num);
    display: inline-block;
    padding: 5px;
    color: var(--lc);
    background: var(--dc);
    border-radius: 13px;
    margin: 0 15px 0 0;
    font-style: italic;
}